.containerCards{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.containerCard{
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 250px;
    padding: 5px;
    margin: 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
}
.containerCard:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

.low{
    background-color: green;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    color: #fff;
    border: none;
    border-radius: 16px;
    box-shadow: 0 3px #999;
    width: 20%;
}
.medium{
    background-color: orange;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    color: #fff;
    border: none;
    border-radius: 16px;
    box-shadow: 0 3px #999;
    width: 20%;
}
.high{
    background-color: red;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    color: #fff;
    border: none;
    border-radius: 16px;
    box-shadow: 0 3px #999;
    width: 20%;
}
.containerCard__delete{
    cursor: pointer;
    display: block;
    background-color:#3117A7;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    margin: 4px 2px;
    border-radius: 16px;
    width: 25%;
}